.container {
    display: flex;
    width: 100%;
    overflow: hidden;
    height: 100vh;
    background-color: var(--background-color);
    padding:1px 4px 4px 1px;
}

.wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.overall {
    display: flex;
    width: 100%;
    height: 100%;
}

.writeup{
    color: var(--main-color);
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.write {
   width: 50%;
   height: 50%;
   display: flex;
   justify-content: center;
   align-items: center;
}

.images {
    color: var(--main-color);
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageBox {
   width: 40%;
   height: 30%;
   background-color: white;
   position: relative;
}

.imageBoxx {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 15%;
    left: 15%;
    background-color: #fee715;
}

.me {
    display: flex;
    position: relative;
    top: 50%;
    left: 50%;
    width: 25rem;
    height: 25rem;
    transform: translate(-50%, -50%);
}
@media screen and (max-width: 760px){
    .overall{
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: space-around;
        width: 100%;
    }

    .writeup {
        width: 100%;
    }

    .images {
        width: 100%;
    }

    .imageBox{
      height: 60%; 
      width: 60%; 
    }

    .me {
        display: flex;
        position: relative;
        top: 50%;
        left: 50%;
        width: 20rem;
        height: 20rem;
        transform: translate(-50%, -50%);
    }
}